/* Application.css is based off the ITCSS methodology by Harry Roberts. */
/* It starts with low-specifity selectors to high-specifity selectors. */
/* The naming convention used is BEM. */
@tailwind base;
@tailwind components;
:root {
  --brandColor: theme("colors.red.500");
  --brandColorDark: theme("colors.red.600");
  --secondaryBrandColor: theme("colors.gray.800");
  --secondaryBrandColorDark: theme("colors.gray.900");
  --textColor: theme("colors.gray.900");
  --textColorInverted: theme("colors.white");
  --ease-in-quad:  cubic-bezier(.55, .085, .68, .53);
  --ease-in-cubic: cubic-bezier(.550, .055, .675, .19);
  --ease-in-quart: cubic-bezier(.895, .03, .685, .22);
  --ease-in-quint: cubic-bezier(.755, .05, .855, .06);
  --ease-in-expo:  cubic-bezier(.95, .05, .795, .035);
  --ease-in-circ:  cubic-bezier(.6, .04, .98, .335);
  --ease-out-quad:  cubic-bezier(.25, .46, .45, .94);
  --ease-out-cubic: cubic-bezier(.215, .61, .355, 1);
  --ease-out-quart: cubic-bezier(.165, .84, .44, 1);
  --ease-out-quint: cubic-bezier(.23, 1, .32, 1);
  --ease-out-expo:  cubic-bezier(.19, 1, .22, 1);
  --ease-out-circ:  cubic-bezier(.075, .82, .165, 1);
  --ease-in-out-quad:  cubic-bezier(.455, .03, .515, .955);
  --ease-in-out-cubic: cubic-bezier(.645, .045, .355, 1);
  --ease-in-out-quart: cubic-bezier(.77, 0, .175, 1);
  --ease-in-out-quint: cubic-bezier(.86, 0, .07, 1);
  --ease-in-out-expo:  cubic-bezier(1, 0, 0, 1);
  --ease-in-out-circ:  cubic-bezier(.785, .135, .15, .86); }

/* Base.css contains unclassed HTML elements */
body {
  @apply font-sans leading-normal antialiased;
  color: var(--textColor); }

a:not([class]) {
  @apply underline;
  color: var(--brandColor); }
  a:not([class]):hover {
    @apply no-underline; }

h1:not([class]), h2:not([class]), h3:not([class]), h4:not([class]), h5:not([class]), h6:not([class]) {
  @apply mb-4;
  @apply text-base font-light text-gray-800; }

p {
  @apply leading-normal; }

article p {
  @apply mb-4; }

/* Objects.css contains cosmetic-free design patterns */
/* Design patterns here, no visual treatments, agnostically named, eg `.ui-list` */
/*
 Check settings.css where some easings are defined as root variables
*/
.animate {
  animation-duration: 1s;
  animation-fill-mode: both; }

@keyframes flashAnimation {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0) scale(0.86); }
  3%, 93% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1); }
  100% {
    opacity: 0;
    transform: translate3d(0, 100%, 0); } }

.flashAnimation {
  animation: flashAnimation 10s var(--ease-in-out-circ);
  animation-fill-mode: both; }
  .flashAnimation:hover {
    animation-play-state: paused; }

@keyframes flashHideAnimation {
  0% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1); }
  25% {
    opacity: .8;
    transform: translate3d(0, -108%, 0) scale(1.05); }
  100% {
    opacity: 0;
    transform: translate3d(0, 100%, 0); } }

.flashHideAnimation {
  animation: flashHideAnimation 1s var(--ease-in-out-circ);
  animation-fill-mode: both; }

@keyframes slideUp {
  0% {
    transform: translate3d(0, 0, 0); }
  100% {
    transform: translate3d(0, -4px, 0); } }

.slideUp {
  animation: slideUp 1s var(--ease-in-out-circ) both; }

.slideUpOnHover:hover {
  animation: slideUp 1s var(--ease-in-out-circ) both; }

@keyframes slideDown {
  0% {
    transform: translate3d(0, 0, 0); }
  100% {
    transform: translate3d(0, 4px, 0); } }

.slideDown {
  animation: slideDown 1s var(--ease-in-out-circ) both; }

.slideDownOnHover:hover {
  animation: slideDown 1s var(--ease-in-out-circ) both; }

@keyframes slideLeft {
  0% {
    transform: translate3d(0, 0, 0); }
  100% {
    transform: translate3d(-4px, 0, 0); } }

.slideLeft {
  animation: slideLeft 1s var(--ease-in-out-circ) both; }

.slideLeftOnHover:hover {
  animation: slideLeft 1s var(--ease-in-out-circ) both; }

@keyframes slideRight {
  0% {
    transform: translate3d(0, 0, 0); }
  100% {
    transform: translate3d(4px, 0, 0); } }

.slideRight {
  animation: slideRight 1s var(--ease-in-out-circ) both; }

.slideRightOnHover:hover {
  animation: slideRight 1s var(--ease-in-out-circ) both; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.fadeIn {
  animation: fadeIn 1s var(--ease-in-cubic) both; }

.fadeInOnHover:hover {
  animation: fadeIn 1s var(--ease-in-cubic) both; }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.fadeOut {
  animation: fadeOut 1s var(--ease-out-circ) both; }

.fadeOutOnHover:hover {
  animation: fadeOut 1s var(--ease-out-circ) both; }

@keyframes zoomIn {
  0% {
    transform: scale(1, 1); }
  100% {
    transform: scale(1.1, 1.1); } }

.zoomIn {
  animation: zoomIn 1s var(--ease-in-cubic) both; }

.zoomInOnHover:hover {
  animation: zoomIn 1s var(--ease-in-cubic) both; }

@keyframes zoomOut {
  0% {
    transform: scale(1, 1); }
  100% {
    transform: scale(0.9, 0.9); } }

.zoomOut {
  animation: zoomOut 1s var(--ease-out-cubic) both; }

.zoomOutOnHover:hover {
  animation: zoomOut 1s var(--ease-out-cubic) both; }

@keyframes heartbeat {
  0% {
    transform: scale(1); }
  25% {
    transform: scale(0.97); }
  35% {
    transform: scale(0.9); }
  45% {
    transform: scale(1.1); }
  55% {
    transform: scale(0.9); }
  65% {
    transform: scale(1.1); }
  75% {
    transform: scale(1.03); }
  100% {
    transform: scale(1); } }

.heartbeat {
  animation: heartbeat 2s var(--ease-out-cubic) infinite; }

.heartbeatOnHover:hover {
  animation: heartbeat 2s var(--ease-in-out-circ) infinite; }

